.frameAction {
  display: grid;
  grid-template-columns: 45% 1fr 45%;
  align-items: center;
  justify-items: center;
  width: auto;
  margin: auto;
  overflow: hidden;
  padding: 25px;
}

.frameActionMobile {
  display: none;
}

.textAction {
  width: 500px;
  font-family: 'Montserrat';
  font-size: 1rem;
}

.imgOfAction img {
  width: 500px;
  height: 300px;
  border-radius: 20px;
  object-fit: fill;
}

@media screen and (max-width: 744px) {
  .frameAction {
    display: none;
  }
  .frameActionMobile {
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    margin: auto;
    padding: 25px;
    overflow: hidden;
  }
  .imgOfAction img {
    height: 190px;
    min-width: 230px;
    max-width: 315px;
    border-radius: 20px;
  }
  .textAction {
    min-width: 230px;
    max-width: 315px;
    z-index: -5;
    font-size: 0.9rem;
  }
}

@media screen and (max-width: 576px) {
  .frameActionMobile {
    padding: 20px;
  }
  .textAction {
    min-width: 20vw;
    font-size: 0.8rem;
  }
}
@media screen and (max-width: 376px) {
  .frameActionMobile {
    display: flex;
    flex-direction: column;
    padding: 15px;
  }
  .LineRedVertMobile {
    display: none;
  }
  .imgOfAction img {
    width: 280px;
    max-height: 170px;
    border-radius: 10px;
  }
  .textAction {
    max-width: 260px;
    margin-top: 5px;
    font-size: 0.7rem;
    text-align: justify;
  }
}
